html {
  font-size: 62.5%;
  box-sizing: border-box; }

*,
*:before,
*:after {
  box-sizing: inherit; }

body {
  font-family: "IBM Plex Sans", sans-serif;
  font-size: 1.6rem;
  background-color: #002855;
  overflow: hidden;
  position: fixed;
  margin-left: 0; }
  @media (min-width: 768px) {
    body {
      margin-left: 20vw; } }

p {
  font-size: 2rem;
  color: #90bdd0; }

.contenedor {
  max-width: 120rem;
  width: 100%; }

a {
  text-decoration: none; }

img {
  max-width: 100%; }

h1,
h2,
h3 {
  margin: 0;
  color: #ffc300;
  font-family: "Emberly", sans-serif; }

h1 {
  font-size: 7rem;
  font-weight: 700; }

h2 {
  font-size: 3.6rem; }

h3 {
  font-size: 3rem; }

.bg-verde {
  background-color: #4cb8b3; }

.bg-amarillo {
  background-color: #fef46d; }

@media (min-width: 480px) {
  .header {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    gap: 5rem; } }

.header .titulo,
.header .contenedor-navegacion {
  display: flex; }
  @media (min-width: 480px) {
    .header .titulo,
    .header .contenedor-navegacion {
      height: calc(100vh - 10px);
      gap: 2rem;
      flex-flow: column;
      justify-content: center; } }

.header .titulo {
  margin-left: 5rem;
  align-items: center; }
  @media (min-width: 768px) {
    .header .titulo {
      margin-left: 0rem; } }
  .header .titulo h1 {
    padding-left: 2rem;
    padding-top: 3rem;
    font-size: 5rem;
    text-shadow: 5px 5px 5px rgba(0, 0, 0, 0.5);
    animation-name: clear-text-shadow;
    animation-duration: .3s;
    animation-delay: 2s;
    animation-fill-mode: both; }
    @media (min-width: 480px) {
      .header .titulo h1 {
        font-size: 7rem;
        padding-top: initial;
        padding-left: unset; } }

.header .contenedor-navegacion {
  align-items: flex-start;
  padding-left: 12rem;
  padding-top: 10rem; }
  @media (min-width: 480px) {
    .header .contenedor-navegacion {
      padding-top: unset;
      padding-left: 8rem; } }

.header .navegacion {
  display: grid;
  gap: 2rem; }
  .header .navegacion a {
    text-shadow: 5px 5px 5px rgba(0, 0, 0, 0.5);
    color: #fff;
    font-family: "Emberly", sans-serif;
    font-size: 5rem;
    text-align: right; }
    .header .navegacion a:hover {
      color: #ffc300; }
  .header .navegacion .trabajo,
  .header .navegacion .cv,
  .header .navegacion .contacto {
    animation-name: appear;
    animation-duration: 0s;
    animation-fill-mode: both; }
  .header .navegacion .trabajo {
    animation-delay: 1.4s; }
  .header .navegacion .cv {
    animation-delay: 1.6s; }
  .header .navegacion .contacto {
    animation-delay: 1.8s; }

.line {
  border: 1px solid #ffc300;
  box-shadow: 5px 5px 5px rgba(0, 0, 0, 0.5); }

.line-mid {
  border: 1px solid #ffc300;
  box-shadow: 5px 5px 5px rgba(0, 0, 0, 0.5);
  visibility: visible;
  position: absolute;
  width: 1px;
  height: 0;
  margin-top: -100vh;
  margin-left: 2rem;
  animation-name: showline-mid;
  animation-duration: 2s;
  animation-fill-mode: both;
  animation-delay: 0; }
  @media (min-width: 480px) {
    .line-mid {
      margin-top: -110vh;
      margin-left: 50%;
      position: absolute; } }

.line-trabajos {
  border: 1px solid #ffc300;
  box-shadow: 5px 5px 5px rgba(0, 0, 0, 0.5);
  visibility: visible;
  position: absolute;
  width: 0;
  height: 1px;
  margin-top: -18.5rem;
  margin-left: 2rem;
  animation-name: showline-trabajos;
  animation-duration: 0.5s;
  animation-fill-mode: both;
  animation-delay: 1s; }
  @media (min-width: 480px) {
    .line-trabajos {
      margin-top: calc(-50vh - 7.5rem);
      margin-left: 50%;
      position: absolute; } }

.line-cv {
  border: 1px solid #ffc300;
  box-shadow: 5px 5px 5px rgba(0, 0, 0, 0.5);
  visibility: visible;
  position: absolute;
  width: 0;
  height: 1px;
  margin-top: calc(-18.5rem + 7.5rem);
  margin-left: 2rem;
  animation-name: showline-cv;
  animation-duration: 0.5s;
  animation-fill-mode: both;
  animation-delay: 1.2s; }
  @media (min-width: 480px) {
    .line-cv {
      margin-top: -50vh;
      margin-left: 50%;
      position: absolute; } }

.line-contacto {
  border: 1px solid #ffc300;
  box-shadow: 5px 5px 5px rgba(0, 0, 0, 0.5);
  visibility: visible;
  position: absolute;
  width: 0;
  height: 1px;
  margin-top: calc(-18.5rem + 7.5rem * 2);
  margin-left: 2rem;
  animation-name: showline-contacto;
  animation-duration: 0.5s;
  animation-fill-mode: both;
  animation-delay: 1.4s; }
  @media (min-width: 480px) {
    .line-contacto {
      margin-top: calc(-50vh + 7.5rem);
      margin-left: 50%;
      position: absolute; } }

@keyframes showline-mid {
  0% {
    height: 0; }
  100% {
    height: 150vh; } }

@keyframes showline-trabajos {
  0% {
    visibility: hidden;
    width: 0; }
  100% {
    width: 10rem;
    visibility: visible; } }

@keyframes showline-cv {
  0% {
    visibility: hidden;
    width: 0; }
  100% {
    width: 18rem;
    visibility: visible; } }

@keyframes showline-contacto {
  0% {
    visibility: hidden;
    width: 0; }
  100% {
    width: 10rem;
    visibility: visible; } }

@keyframes clear-text-shadow {
  0% {
    text-shadow: 2px 2px 8px #000000; }
  100% {
    text-shadow: 0 0 0px #000000; } }

@keyframes clear-box-shadow {
  0% {
    text-shadow: 2px 2px 8px #000000; }
  100% {
    text-shadow: 0 0 0px #000000; } }

@keyframes appear {
  0% {
    visibility: hidden; }
  100% {
    visibility: visible; } }

@keyframes ball1 {
  0% {
